


/* 탑 캐러샐 */
.swiper-back{
  width: 100%;
  height: 300px;
  margin-top: 75px;
  overflow: hidden;
  position: relative;
}
.swiper-contents{
  height: 100%;
  transition: all 1s;
}

.swiper-card{
  width: 100%;
  max-width: 1060px;
  margin: 0px 12px;
  height: 100%;
  position: relative;
}
.swiper-card img{
  width: 100%;
  height: 100%;
}

.top-swiper-btn{
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 120px;
  width: 30px;
  height: 60px;
  opacity: .5;
  border-radius: 15px;
  background-color: #fff;
  font-size: 16px;
  z-index: 10;
}
.top-swiper-before-btn{
  left: calc((100% - 1210px)/2);
}
.top-swiper-next-btn{
  right: calc((100% - 1200px)/2);
}
.top-swiper-btn-svg{
  width: 16px;
  height: 16px;
}

.swiper-text-box{
  position: absolute;
  bottom: 28px;
  width: 330px;
  height: 146px;
  border-radius: 4px;
  background-color: #fff;
  opacity: 1;
  text-align: left;
  left: 34px;
}
.swiper-text-box-title{
  margin-left: 20px;
  margin-right: 20px;
  font-size: 20px;
  line-height: 1.5;
  margin-top: 20px;
  font-weight: 700;
  color: #333;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  word-break: break-all;
}
.swiper-text-box-sub-title{
  margin: 0 20px;
  height: 44px;
  font-size: 14px;
  line-height: 1.64;
  color: #333;
  font-weight: 500;
}
.swiper-text-box-hr{
  height: 1px;
  margin: 0;
  border: none;
  background-color: #ececec;
}
.swiper-text-box-link>div{
  color: #36f;
  height: 40px;
  font-size: 15px;
  padding: 6px 8px;
  margin: 6px 0 0 13px;
  min-width: 64px;
  justify-content: center;
  vertical-align: middle;
  font-weight: 700;
}

